import Link from "next/link";
import { getCaseById } from "@/lib/caseStore";

import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Button } from "@/components/ui/button";

import DecisionPanel from "./DecisionPanel";

function fmtTs(iso: string) {
  try {
    const d = new Date(iso);
    return d.toISOString().replace("T", " ").replace(".000Z", "Z");
  } catch {
    return iso;
  }
}

function riskTone(label: string) {
  if (label === "BLOCK") return { pill: "bg-red-600 text-white", box: "bg-red-50 border-red-200" };
  if (label === "ESCALATE") return { pill: "bg-orange-500 text-white", box: "bg-orange-50 border-orange-200" };
  return { pill: "bg-slate-800 text-white", box: "bg-slate-50 border-slate-200" };
}

export default async function CaseDetailPage({
  params,
}: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await params;
  const c = getCaseById(id);
  if (!c) throw new Error("Case not found");

  const tone = riskTone(c.risk_label);
  const requestedAt = fmtTs(c.created_at);

  return (
    <div className="min-h-screen bg-muted/40 p-6">
      <div className="mx-auto w-full max-w-4xl space-y-4">
        <div className="flex items-center justify-between">
          <div className="flex gap-2">
            <Link href="/cases">
              <Button variant="secondary">Back to Inbox</Button>
            </Link>
            <Link href="/">
              <Button variant="ghost">Home</Button>
            </Link>
          </div>

          <div className="text-sm text-muted-foreground">Requested: {requestedAt}</div>
        </div>

        <Card className="shadow-sm">
          <CardHeader className="space-y-3">
            <div className="flex flex-wrap gap-2">
              <Badge className="bg-sky-600 text-white hover:bg-sky-600">SAFETY_GUIDANCE</Badge>
              <Badge className={tone.pill + " hover:" + tone.pill}>{c.risk_label}</Badge>
              <Badge variant="secondary">{c.status}</Badge>
            </div>

            <div className="space-y-1">
              <div className="text-xl font-semibold">{c.id}</div>
              <div className="text-sm text-muted-foreground">
                Review the intercepted tool call. Decide with policy and minimum-necessary access in mind.
              </div>
            </div>
          </CardHeader>

          <CardContent className="space-y-6">
            <div className="grid gap-6 md:grid-cols-3">
              <div className="md:col-span-2 space-y-6">
                <Card className="border">
                  <CardHeader className="pb-3">
                    <div className="text-sm font-semibold">Context</div>
                  </CardHeader>
                  <CardContent className="space-y-2 text-sm">
                    <div>
                      <span className="text-muted-foreground">User:</span> {c.user_display}
                    </div>
                    <div>
                      <span className="text-muted-foreground">User message:</span> {c.user_message}
                    </div>
                    <div>
                      <span className="text-muted-foreground">Tool:</span>{" "}
                      <span className="font-mono text-xs">{c.tool_name}</span>
                    </div>
                  </CardContent>
                </Card>

                <Card className={"border " + tone.box}>
                  <CardHeader className="pb-3">
                    <div className="flex items-center justify-between">
                      <div className="text-base font-semibold">Risk Assessment</div>
                      <Badge className={tone.pill + " hover:" + tone.pill}>
                        {c.risk_label} ({c.risk_score})
                      </Badge>
                    </div>
                  </CardHeader>

                  <CardContent className="space-y-4">
                    <div className="text-sm">
                      <div className="font-semibold">Reason</div>
                      <div className="text-muted-foreground">{c.risk_rationale}</div>
                    </div>

                    <div className="grid gap-3 md:grid-cols-2">
                      <div className="text-sm">
                        <div className="font-semibold">Confidence</div>
                        <div className="text-muted-foreground">{Math.min(99, Math.max(1, c.risk_score))}%</div>
                      </div>
                      <div className="text-sm">
                        <div className="font-semibold">Escalation Route</div>
                        <div className="text-muted-foreground">HUMAN REVIEW</div>
                      </div>
                    </div>

                    <div className="text-sm">
                      <div className="font-semibold">Policy refs</div>
                      <div className="flex flex-wrap gap-2 mt-2">
                        {(c.policy_refs || []).map((p) => (
                          <Badge key={p} variant="secondary" className="text-xs">
                            {p}
                          </Badge>
                        ))}
                      </div>
                    </div>

                    <div className="text-sm">
                      <div className="font-semibold">Immediate steps</div>
                      <ul className="list-disc pl-5 text-muted-foreground space-y-1 mt-2">
                        <li>Confirm minimum-necessary access for the tool call.</li>
                        <li>Check policy refs and ensure rationale matches the request.</li>
                        <li>Approve only if execution impact is acceptable and logged.</li>
                        <li>If unclear, request info instead of guessing.</li>
                      </ul>
                    </div>
                  </CardContent>
                </Card>

                <Card className="border">
                  <CardHeader className="pb-3">
                    <div className="text-sm font-semibold">Tool args (redacted)</div>
                  </CardHeader>
                  <CardContent>
                    <pre className="text-xs bg-muted p-3 rounded overflow-auto">
                      {JSON.stringify(c.tool_args_redacted, null, 2)}
                    </pre>
                  </CardContent>
                </Card>

                <Card className="border">
                  <CardHeader className="pb-3">
                    <div className="text-sm font-semibold">Audit trail</div>
                  </CardHeader>
                  <CardContent>
                    <ul className="text-sm space-y-2">
                      {c.history.map((h, idx) => (
                        <li key={idx} className="flex gap-3">
                          <span className="text-xs text-muted-foreground w-48">{h.ts}</span>
                          <span className="text-xs font-mono w-24">{h.actor}</span>
                          <span className="text-xs font-mono w-24">{h.event}</span>
                          <span className="text-sm">{h.detail}</span>
                        </li>
                      ))}
                    </ul>
                  </CardContent>
                </Card>
              </div>

              <div className="space-y-6">
                <Card className="border">
                  <CardHeader className="pb-3">
                    <div className="text-base font-semibold">Decision</div>
                  </CardHeader>
                  <CardContent>
                    <DecisionPanel caseId={c.id} />
                  </CardContent>
                </Card>

                <Card className="border">
                  <CardHeader className="pb-3">
                    <div className="text-sm font-semibold">Decision guidance</div>
                  </CardHeader>
                  <CardContent className="text-sm text-muted-foreground space-y-2">
                    <div>Approve executes the tool call.</div>
                    <div>Reject blocks the action and logs rationale.</div>
                    <div>Request info pauses review and asks for more context.</div>
                  </CardContent>
                </Card>
              </div>
            </div>

            <Separator />

            <div className="text-xs text-muted-foreground">
              Status is the human outcome. Risk label is the model recommendation.
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
